import faculty from "@/data/faculty.json";
import auditors from "@/data/auditors.json";
import staffAdvisors from "@/data/staffAdvisors.json";
import type { UserRole } from "@/lib/roles";

export interface UserRecord {
  id: string;
  username: string;
  password: string;
  name: string;
  role: UserRole;
  department?: string;
  dashboardId?: string;
}

export interface AuthUser {
  id: string;
  username: string;
  name: string;
  role: UserRole;
  department?: string;
}

export const ALL_USERS: UserRecord[] = [
  ...(faculty as UserRecord[]),
  ...(auditors as UserRecord[]),
  ...(staffAdvisors as UserRecord[]),
];

export function findUserByUsername(username: string) {
  return ALL_USERS.find((user) => user.username === username);
}

export function verifyCredentials(username: string, password: string) {
  const user = findUserByUsername(username);
  if (!user || user.password !== password) {
    return null;
  }

  const { id, name, role, department } = user;
  return {
    id,
    username: user.username,
    name,
    role,
    department,
  } satisfies AuthUser;
}
